<template>
  <div>
    <div>
      <div class="item">
        <div class="img">
          <img src="../assets/images/h_phone.png" class="pic">
        </div>
        <input id="userTelphone" type="text" placeholder="手机号码" v-model="userTelphone"
               style="width: 85%;height: 100%;border: none;float: left;float: left;">
      </div>
      <div class="item">
        <div class="img">
          <img src="" class="pic"></div>
        <input id="code" type="text" placeholder="验证码" v-model="checkCode"
               style="width: 65%;height: 100%;border: none;float: left;float: left;">
        <div style="width: 20%;height: 100%;float: left;padding: 5px;">
          <button type="button"
                  style="width:100%;height: 50%;border: 1px solid #4cc7ff;border-radius: 2em;float: right;">获得验证码
          </button>
        </div>
      </div>
      <div class="item">
        <div class="img">
          <img src="../assets/images/h_pwd.png" class="pic"></div>
        <input id="password" type="text" placeholder="密码（至少6位）" v-model="userPassword"
               style="width: 85%;height: 100%;border: none;float: right;float: left;">
      </div>
    </div>
    <div style="width: 100%;height: 40px;display: inline-block;text-align: center;padding: 5px;">
      <button onclick="register();" type="button" style="width: 90%;height: 100%;text-align:center;border-radius: 2em;background-color: #4cc7ff;;
        font-size: 16px;vertical-align:middle;">同意服务条款并注册
      </button>
    </div>
    <div style="width: 100%;text-align: center">
      <a href="#">点击阅读亲亲印象服务条款</a>
    </div>
  </div>
</template>

<script>
  export default {
    name: "registUser",
    data() {
      return {
        userTelphone: '',
        userPassword: '',
        checkCode: ''
      }

    },
    methods: {
      registerUser: function () {
        let _this = this;
        $.ajax({
          url: '<%=basePath%>/saveUser',
          data: {
            "userTelphone": this.userTelphone,
            "userNickName": '',
            "userPassword": this.userPassword,
            "checkCode": this.checkCode,
            "isThreeLogin": '0',
            "phoneDeviceCode": '',
            "phoneDeviceName": '',
            "threeLoginType": '',
            "threeLoginID": '',
            "sex": '',
            "userHead": '',
            "phoneDeviceType": ''
          },
          dataType: "json",
          success: function (data) {
            $.each(data, function (i, item) {
              let result = item.result;
              let userCode = item.userCode;
              let groupId = item.groupId;
              if (result === 1) {

                //window.location.href = "<%=basePath%>/space?groupId=''&userCode=" + userCode;
                _this.$router.push({path:'/home/',query:{userCode:userCode,groupId:''}});
              }
            });
          }
        });
      }
    }
  }
</script>

<style scoped>
  * {
    padding: 0;
    margin: 0;
  }

  .item {
    width: 100%;
    height: 50px;
    float: left;
    display: inline-block;
    border-bottom: 1px solid #d7d7d7;
  }

  .pic {
    width: 100%;
    height: auto;
    float: left;
  }

  .img {
    width: 8%;
    text-align: center;
    display: inline-block;
    float: left;
    padding: 5px;
  }
</style>



